import { Component, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-lomake',
  templateUrl: './lomake.component.html',
  styleUrls: ['./lomake.component.css']
})
export class LomakeComponent implements OnInit {

  @Output() valmis = new EventEmitter<any>();

  nimi: string;
  supersankari: string;
  vaihtoehdot: string [] = [
  'Superhessu',
  'Wonder Woman',
  'Teräsmies',
  'XXX' ];

  constructor() { }

  ngOnInit() {}

  laheta() {
    console.log('Nimi: ' + this.nimi + ' - ' + 'sankarisi: ' + this.supersankari + '. Tämä printti tulee lomake.component.ts:stä');
    this.valmis.emit({
      nimi: this.nimi,
      supersankari: this.supersankari
    });
  }
}



